<template>
  <div>
    <transition name="page" mode="out-in">
      <div :key="$route.params.pageIndex">
        <div v-for="(block, index) in data.pages[$route.params.pageIndex].blocks" class="block" @click="editBlock(index)">
            <div class="html">
              <iframe-block :block="block"></iframe-block>
            </div>

            <div class="absolute">
              <div class="border-top"></div>
              <div class="border-sides"></div>
              <div class="border-bottom"></div>
            </div>
            <div class="tools">
              <strong>{{ block.name }}</strong> | up | down 
            </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import IframeBlock from '../views/IframeBlock'

export default {

  data () {
    return {
      data: window.parent.data,
    }
  },

  methods: {
    editBlock: function (index) {
      window.parent.builder.editBlock(this.$route.params.pageIndex, index);
    }
  },

  components: {
    'iframe-block': IframeBlock,
  }
}
</script>

<style scoped lang="scss">
  .page-enter-active, .page-leave-active {
    transition: opacity 0.2s ease-out, transform 0.2s ease-out;
  }

  .page-enter, .page-leave-to {
    opacity: 0;
    transform: translateX(-15%);
  }

  .block {
    position: relative;
    // opacity: 0.5
  }

  .html {
    display: -webkit-box;
  }

  .absolute {
    position: absolute;
    top: 0;
    left: 0px;
    width: 100%;
    height: 100%;
    display: none;
    z-index: 2;
  }

  .border-top {
    background: #5bc0de;
    height: 4px;
    width: 100%;
    top: 0;
    border-radius: 3px 3px 0px 0px;
    position: sticky;
  }

  .border-sides {
    width: 100%;
    height: calc(100% + -39px);
    border-width: 4px 4px;
    border-style: solid solid;
    border-color: #5bc0de #5bc0de;
    -o-border-image: initial;
    border-image: initial;
    border-top: none;
    border-bottom: 0px;
  }

  .border-bottom {
    height: 4px;
    width: 100%;
    bottom: 0px;
    position: sticky;
    background: rgb(91, 192, 222);
    border-radius: 0px 0px 3px;
  }

  .tools {
    width: 500px;
    height: 35px;
    position: sticky;
    bottom: 0px;
    color: rgb(255, 255, 255);
    z-index: 1;
    margin-bottom: -35px;
    display: none;
    background: rgb(91, 192, 222);
    border-radius: 3px;
    padding: 5px 8px;
    z-index: 2;
  }

  .block:last-child {
    padding-bottom: 34px;
    // margin-top: 20px;
    // display: none;
  }

  .block:hover > .absolute {
    display: inherit
  }
  .block:hover > .tools {
    display: inherit
  }
</style>



